<template>
    <div class="travelMap">
        <div id="main"></div>
    </div>
</template>
<script>
import geoData from '@/assets/data.json'
import {travelview} from '@/api/api'
export default {
    data() {
        return {
        }
    },
    created(){
        travelview().then((res)=>{
            if(res.data.status===200){
                let { points, linesData} = res.data.data
                this.draw(points, linesData)
            }
        })
    },
    methods: {
       draw(points,linesData){
        let myMap = this.$echarts.init(document.getElementById('main'))
        this.$echarts.registerMap('china', geoData) //地图注册
        let option = {
            backgroundColor: 'rgb(121, 145, 209)', // 地图背景色
            geo: {
                map: 'china',
                aspectScale: 0.75, // 地图长宽比
                zoom: 1.1, //视角缩放比
                itemStyle: {
                    // 常规状态
                    normal: {
                        backgroundColor: 'rgb(147, 235, 248)',
                        borderWidth: 1,
                        areaColor: {
                            type: 'radial',//镜像渐变
                            x: 0.5,
                            y: 0.5,
                            r: 0.8,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: '#09132c' // 0%处的颜色
                                },
                                {
                                    offset: 1,
                                    color: '#274d68' // 100%处的颜色
                                }
                            ],
                            globalCoord: true

                        },
                        shadowColor: 'rgb(58,120,192)',
                        // shadowColor:'rgb(244,164,96)',
                        shadowOffsetX: 10,
                        shadowOffsetY: 11,
                    },
                    // 高亮状态
                    emphasis: {
                        areaColor: 'rgb(46, 229, 206)', //选中颜色
                        borderWidth: 0.1
                    }
                },
                regions: [{
                    name: '南海诸岛',
                    itemStyle: {
                        opacity: 0
                    }
                }]
            },
            series: [
                // 地图图标的相关配置
                {
                    map: 'china',
                    type: 'map',
                    zoom: 1.1,
                    // 字体配置
                    label: {
                        normal: {
                            show: true,
                            textStyle: {
                                color: '#1DE986'
                            },
                        },
                        emphasis: {
                            textStyle: {
                                color: 'rgb(184,185,12)'
                            }
                        }
                    },
                    itemStyle: {
                        // 常规状态
                        normal: {
                            backgroundColor: 'rgb(147, 235, 248)',
                            borderWidth: 1,
                            areaColor: {
                                type: 'radial',//镜像渐变
                                x: 0.5,
                                y: 0.5,
                                r: 0.8,
                                colorStops: [
                                    {
                                        offset: 0,
                                        color: '#09132c' // 0%处的颜色
                                    },
                                    {
                                        offset: 1,
                                        color: '#274d68' // 100%处的颜色
                                    }
                                ],
                                globalCoord: true

                            },
                        },
                        // 高亮状态
                        emphasis: {
                            areaColor: 'rgb(46, 229, 206)', //选中颜色
                            borderWidth: 0.1
                        }
                    }
                },
                // 散点图配置
                {
                    type:'effectScatter',//散点图
                    coordinateSystem:'geo',
                    showEffectOn:'render',
                    symbolSize:10,//散点大小
                    zlevel:1,
                    // data:[{ value: [118.8062, 31.9208], itemStyle: { color: '#4ab2e5' } }],
                    data:points,
                    rippleEffect:{
                        // 涟漪特效配置
                        period:15, //周期
                        scale:4,
                        brushType:'fill' //绘制方式
                    }
                },
                // 动画线路配置(线图)
                {
                    type:'lines',
                    // data:[{ coords: [[118.8062, 31.9208],[119.4543, 25.9222]], lineStyle: { color: '#4ab2e5' } }],
                    data:linesData,
                    zlevel:2,
                    effect:{
                        show:true,
                        period:4,
                        symbol:'arrow',
                        symbolSize:7,
                        tailLength:0.4
                    },
                    lineStyle:{
                        normal:{
                            width:1,
                            curveness:0.3,
                            opacity:0.1
                        }
                    }
                }
            ]
        }
        myMap.setOption(option)
       }
    }
}
</script>

<style lang="less">
.travelMap {
    width: 100%;

    #main {
        height: 600px;
    }
}
</style>